<template>
<div>
<!-- class="white--text" @click="rerender()" -->
<v-toolbar color="primary" absolute app>

  <v-toolbar-title class="text-uppercase white--text" style="cursor: pointer">
    <v-layout align-center  @click="rerender()">
<!--    <v-avatar tile size="20" class="mx-2"><img src="/img/bot.png" /></v-avatar> -->
    <span class="font-weight-black">國衛院</span><span class="font-weight-thin">蟲卵辨識Vision</span><span class="font-weight-black">..緯(Lat)={{mpos.lat}} , 經(Lon) ={{mpos.lon}}</span>
    <p>{{error}}</p>
  </v-layout>
  </v-toolbar-title>
  <v-spacer></v-spacer>
  <span class="caption mx-3">
    <v-btn-toggle :value="localeSelect" mandatory>
    <v-btn small flat @click="changeLocale('en')">English</v-btn>
    <v-btn small flat @click="changeLocale('es')">西文</v-btn>
    </v-btn-toggle>
  </span>

 <!-- <v-toolbar-items class="hidden-sm-and-down">
    <v-btn
    v-for="(item) in menu[localeSelect]"
    :key="item.title"
    :to="item.link"
    flat dark
  >{{ item.title }}</v-btn>
    </v-toolbar-items> -->

    <v-menu class="hidden-md-and-up">
      <v-toolbar-side-icon slot="activator"></v-toolbar-side-icon>
      <v-list>
        <v-list-tile v-for="item in menu[localeSelect]" :key="item.title" :to="item.link">
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>

</v-toolbar>

</div>
</template>

<script>
import { eventBus, mpos } from '../main.js'
import i18n from '@/plugins/i18n'

export default {
  components: {
  },
  data () {
    return {
      mpos,
      error: '',
      menu: {
        0: [
          { title: 'Vision', link: '/' },
          { title: 'Guide', link: '/guide' },
          { title: 'About', link: '/about' }
        ],
        1: [
          { title: 'Visión', link: '/' },
          { title: 'Guía', link: '/guide-es' },
          { title: 'Acerca de...', link: '/about-es' }
        ]
      },
      languages: [
        { title: 'en', name: 'English' },
        { title: 'es', name: 'es' }
      ],
      loading: false
    }
  },
  computed: {
    localeSelect () {
      if (this.$i18n.locale === 'zh-TW') {
        return 1
      } else {
        return 0
      }
    }
  },
  created () {
    eventBus.$on('appLoading', () => {
      this.loading = true
    })
  },
  mounted () {
    this.myFunction()
  },
  methods: {
    rerender () {
      eventBus.$emit('forceRerender')
    },
    menuItems () {
      return this.menu
    },
    changeLocale (locale) {
      i18n.locale = locale
    },

    myFunction: function () {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.showPosition)
      } else {
        this.error = 'Geolocation is not supported.'
      }
    },
    showPosition: function (position) {
      mpos.lat = position.coords.latitude
      mpos.lon = position.coords.longitude
    }
  }
}
</script>
